.center-click {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.main-form {
    display: flex;
    /* margin: 0 auto; */
}

.popup-overlay {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100vh;
    z-index: 2;
    background: rgba(0, 0, 0, 0.5);
    /* display: none; */
}
.popup {
    display: flex;
    position: absolute;
    margin: auto;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    /* transform: translate(-50%, -50%) scale(1.15); */
    width: 300px;
    height: 440px;
    background: #f5f5f5;
    z-index: 3;
    opacity: 100;
    box-shadow: 5px 5px 3px rgba(0, 0, 0, 0.2);
    /* transition: transform 300ms ease-in-out, opacity 300ms ease-in-out; */
    /* transition: opacity 3s; */
}

.login {
    margin: auto;
}

