.popup .popup-close {
    position: absolute;
    margin: auto;
    top: -10px;
    right: -10px;

    width: 30px;
    height: 30px;
    background: #555;
    color: #f5f5f5;
    font-size: 25px;
    font-weight: 600;
    text-align: center;
    border-radius: 50%;
    cursor: pointer;
}
.popup .login .avatar {
    margin: 30px 0px 20px;
    text-align: center;
}
.popup .login .avatar img {
    width: 120px;
    border-radius: 50%;
}
.popup .login .header {
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    color: #222;
    margin: 20px 0px;
}
.popup .login .element {
    padding: 8px 20px;
}
.popup .login .element label {
    display: block;
    font-size: 14px;
    color: #222;
    margin-bottom: 5px;
}
.popup .login .element input {
    width: 100%;
    padding: 8px 10px;
    box-sizing: border-box;
    outline: none;
    border: 1px solid #aaa;
    background: #eee;
    border-radius: 5px;
}
.popup .login .element button {
    margin-top: 5px;
    width: 100%;
    padding: 10px 0px;
    text-transform: uppercase;
    outline: none;
    border: none;
    font-size: 15px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    /* background: #4889da; */
    color: #f5f5f5;
}

.register {
    height: 530px;
}

.popup {
    animation: slideInDown; /* referring directly to the animation's @keyframe declaration */
    animation-duration: 1s; /* don't forget to set a duration! */
}
